'use strict';

var DATA = require('../data/Countries.json');

var _ = require('underscore');

var Responder = require('./Responder');

var findByString = function findByString(prop, value) {
  var filtered = _.filter(DATA, function (c) {
    if (_.isEqual(c[prop], value)) {
      return c;
    }
  });

  return Responder(filtered);
};

var findInArray = function findInArray(prop, value) {
  var filtered = _.filter(DATA, function (c) {
    if (_.contains(c[prop], value)) {
      return c;
    }
  });

  return Responder(filtered);
};

var findArrayMultiple = function findArrayMultiple(prop, value) {
  var filtered = _.filter(DATA, function (c) {
    return _(c[prop]).values().some(function (v) {
      return v === value;
    });
  });

  return Responder(filtered);
};

var findArrayIndex = function findArrayIndex(prop, value, index) {
  var filtered = _.filter(DATA, function (c) {
    if (_.isEqual(c[prop][index], value)) {
      return c;
    }
  });

  return Responder(filtered);
};

var findArrayEqual = function findArrayEqual(prop, values) {
  var filtered = _.filter(DATA, function (c) {
    if (_.isEqual(c[prop], values)) {
      return c;
    }
  });

  return Responder(filtered);
};

var findInObject = function findInObject(prop, value, index) {
  var match = {};
  match[value] = true;

  var filtered = _.filter(DATA, function (val) {
    return match[val[prop][index]];
  });

  return Responder(filtered);
};

var findAll = function findAll() {
  return Responder(DATA);
};

module.exports = {
  findByString: findByString,
  findArrayEqual: findArrayEqual,
  findArrayIndex: findArrayIndex,
  findArrayMultiple: findArrayMultiple,
  findInArray: findInArray,
  findInObject: findInObject,
  findAll: findAll
};